import { _decorator, Component, Node, Prefab, Vec3, instantiate } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('GameCtrl')
export class GameCtrl extends Component {
    @property(Prefab)
    public enemyWave1: Prefab [] = [];
    @property(Prefab)
    public enemyWave2: Prefab [] = [];
    @property(Prefab)
    public enemyWave3: Prefab [] = [];
    public spawnFunc1 = [false, false, false, false, false, false, false, false, false, false];

    start() {
        this.turnSpawn();
    }

    update(deltaTime: number) {
        
    }
    
    randomWave1(){
        let availableFunctions = this.spawnFunc1.reduce((acc, spawned, index) => {
            if (!spawned) acc.push(index);
            return acc;
        }, []);

        if(availableFunctions.length === 0) return;

        let randomIndex = availableFunctions[Math.floor(Math.random() * availableFunctions.length)];

        this.spawnFunc1[randomIndex]  = true;

        switch(randomIndex){
            case 1:
                break;
            case 2:
                break;
            case 3:
                break;
            case 4:
                break;
            case 5:
                break;
            case 6:
                break;
            case 7:
                break;
            case 8:
                break;
            case 9:
                break;
            case 0:
                break;
            default:
                break;
        }
    }

    turnSpawn(){
        for(let i = 0; i < 1; i++){
            for (let j = 0; j < 5; j++) {
                let enemyPre = this.enemyWave1[i];
                let prefabInstance = instantiate(enemyPre);
                prefabInstance.parent = this.node.parent;
        
                // Tính toán vị trí mới cho mỗi prefabInstance
                let offsetX = 120 * (j - 2); // Điều chỉnh khoảng cách giữa các vật thể
                let offsetY = 60 * j; // Điều chỉnh khoảng cách giữa các hàng của các vật thể
                prefabInstance.setWorldPosition(this.node.worldPosition.x + offsetX, this.node.worldPosition.y + 60, this.node.worldPosition.z);
                console.log('da spawn', prefabInstance);
            }
        }
    }
}


